<template>
  <el-container id="commonTable">
    <el-main>
      <el-table
        ref="table"
        :data="tabeldata"
        :height="height"
        :max-height="maxHeight"
        border
        stripe
        tooltip-effect="light"
        :size="tableSize"
        :style="{ width: tableWidth }"
        :header-cell-style="{ background: '#f6f8f9' }"
        :show-header="true"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          v-if="showCheckBox"
          type="selection"
          width="45"
        ></el-table-column>
        <template v-for="(item, index) in columns">
          {{item}}
          <el-table-column
            v-if="!item.hidden"
            :key="index"
            :fixed="item.fixed"
            :prop="item.prop"
            :label="item.label"
            :align="item.align ? item.align : 'center'"
            :width="item.width"
            :show-overflow-tooltip="true"
          >
            <template #default="{ row }">
              <span
                v-html="
                  item&&item.formatter
                    ? formatter(row, row[item.prop], item)
                    : formatterValue(row, row[item.prop], item)
                "
              ></span>
            </template>
          </el-table-column>
        </template>
        <slot name="table_operation" />
      </el-table>
    </el-main>

    <el-footer
      v-if="showPagination"
      id="simpleTableFooter"
      style="height: 42px; padding-top: 6px; background: white"
    >
      <el-pagination
        style="text-align: right; margin: 0"
        :current-page="pagination.pageNo"
        :page-size="pagination.limit"
        :page-sizes="pagination.sizes"
        :total="pagination.total"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </el-footer>
  </el-container>
</template>

<script>
  //TODO: 如果实体对应的数据库表已经存在数据记录，则不允许修改引用实体，防止数据错乱！！

  export default {
    name: 'SimpleTable',
    props: {
      tableWidth: {
        type: [String, Number],
        default: '100%',
      },
      // columns: Array,
      // data: Array,
      showPagination: {
        type: Boolean,
        default: true,
      },
      showCheckBox: {
        type: Boolean,
        default: false,
      },
      pagination: {
        type: Object,
        default: () => {
          return {
            pageNo: 0,
            limit: 8,
            total: 0,
          }
        },
      },
      height: {
        type: String,
        default: 'auto',
      },
      maxHeight: {
        type: Number,
        default: 2000,
      },
      tableSize: {
        type: String,
        default: 'medium',
      },
    },
    data() {
      return {
        columns: [],
        tabeldata: [],
      }
    },
    mounted() {
      // this.tabeldata = this.data;
    },
    methods: {
      updatable(data, columns) {
        if (columns) {
          this.columns = columns
        }
        console.log(data)
        console.log(this.columns)
        this.tabeldata = data
        this.$refs.table.doLayout()
      },
      handleSelectionChange(val) {
        this.$emit('handleSelectionChange', val)
      },
      handleSizeChange(val) {
        this.$emit('handleSizeChange', val)
      },
      handleCurrentChange(val) {
        this.$emit('handleCurrentChange', val)
      },
      formatterValue(row, cellValue) {
        return cellValue
      },
      formatter(row, cellValue, item) {
        const methods = this.$options.methods
        return methods[item.formatter](cellValue)
      },
      formatterProcess(cellValue) {
        let obj = {
          type: '',
          text: '',
        }
        switch (cellValue) {
          case 9:
            obj.text = '完成'
            obj.type = 'success'
            break
          case 0:
            obj.text = '未开始'
            obj.type = 'info'
            break
          case -1:
            obj.text = '驳回'
            obj.type = 'danger'
            break
          default:
            obj.text = cellValue
            obj.type = ''
        }
        let str = `<span class="el-tag el-tag--light el-tag--small el-tag--${obj.type}">${obj.text}</span>`
        return str
      },
    },
  }
</script>

<style lang="scss" scoped>
  .el-table {
    // 解决table组件内容滚动时页面滚动条同步滚动
    overflow: auto;
    // 必须设置
    position: relative;

    ::v-deep .el-table__fixed-header-wrapper thead th > .cell {
      white-space: nowrap !important; /* 禁止表头换行 */
    }

    ::v-deep .el-table__header-wrapper thead th > .cell {
      white-space: nowrap !important; /* 禁止表头换行 */
    }

    ::v-deep .el-table__body-wrapper {
      //height: 100% !important;
    }
  }
</style>
